.book-btn {
  @color: @black;
  @bg-color: @yellow;

  position: relative;

  display: inline-block;
  padding-top: 13px;
  padding-right: 61px;
  padding-bottom: 13px;
  padding-left: 18px;

  font-size: 17px;
  font-weight: bold;
  color: @color;
  text-align: center;
  text-decoration: none;

  background-color: @bg-color;

  border-radius: 5px;

  transition: background 0.2s;

  &:hover,
  &:focus {
    background-color: darken(@bg-color, 5%);
  }

  &:active {
    background-color: darken(@bg-color, 2%);
    color: fade(@color, 50%);
  }

  &::after {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;

    width: 44px;
    background-image: url(../img/icon/book-btn.svg);
    background-size: 17px;
    background-repeat: no-repeat;
    background-position: center;

    border-left: 1px solid white;
  }

  &--loading::after {
    background-image: none;
  }

  &--loading::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 15px;
    margin-top: -8px;

    width: 17px;
    height: 17px;
    background-image: url(../img/icon/loading.png);
    background-size: 17px;
    background-repeat: no-repeat;
    background-position: center;
    animation: loader 1s infinite steps(8);
  }

  &--success::after {
    background-image: url(../img/icon/book-btn-success.svg);
  }
}


@keyframes loader {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
